<template>

   
<v-card
    class="mx-auto"
   
    outlined
    v-on:click="openDetail(movie.imdbID)"
  >
    <v-list-item two-line>

        <v-list-item-avatar
            tile
            size="120">
            <v-img v-bind:src="movie.Poster" contain></v-img>
        </v-list-item-avatar>
       
        <v-list-item-content>
            <!-- <div class="text-overline mb-4">
            OVERLINE
            </div> -->
            <v-list-item-title class="text-h5 mb-1">
                   {{movie.Title}}
            </v-list-item-title>
            <v-list-item-subtitle>
                   {{movie.Year}} - {{movie.Type}}
            </v-list-item-subtitle>
      </v-list-item-content>

      
    </v-list-item>

   
  </v-card>

</template>

<script>



export default {
  name: 'ListItem',
  props: ['movie'],
  methods : {
      openDetail: function(movieId)
      {
          console.log(movieId)
          this.$router.push(`detail/${movieId}`)
      }
  }

    
  
  
}
</script>
