<template>
 <v-row>
  <v-col cols="8">
    <v-text-field label="Enter Movie Name" v-model="searchedMovie"></v-text-field>
  </v-col>
  <v-col cols="4">
      <v-btn  color="primary" block  v-on:click="searchMovie"> Search</v-btn>
  </v-col>
 </v-row>
</template>

<script>


export default {
  name: 'Search',
  data(){
      return{
          searchedMovie:''
      }
  },
  methods :
  {
      searchMovie:function(){
          this.$emit('callApi', this.searchedMovie)
      }
  }
}
</script>
