<template>
<div v-if="movies">
  <v-card
   
    class="mx-auto my-12"
    max-width="600"
    
  >
    <template slot="progress">
      <v-progress-linear
        color="deep-purple"
        height="10"
        indeterminate
      ></v-progress-linear>
    </template>

   
    <v-img  height="250" v-bind:src="movies.Poster" contain ></v-img>

    <v-card-title>{{movies.Title}}</v-card-title>

    <v-card-text>
      <v-row
        align="center"
        class="mx-0"
      >
        <v-rating
          :value="parseFloat(movies.imdbRating)"
          color="amber"
          dense
          half-increments
          readonly
          size="14"
          length="10"
        ></v-rating>

        <div class="grey--text ms-4">
         {{movies.imdbRating}} ( {{movies.Metascore}}  )
        </div>
        <div>
        <v-dialog
           v-model="dialog"
            width="500"
           >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="red lighten-2"
                dark
                v-bind="attrs"
                v-on="on"
              >
               View Rating
              </v-btn>
            </template>

            <v-card>
        <v-card-title class="text-h5 grey lighten-2">
          Rating and review
        </v-card-title>

        <v-card-text v-for="review in movies.Ratings" :key="review.Source"> 
         <h6> {{review.Source}} </h6>
          <p>{{review.Value}}</p>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="dialog = false"
          >
            I accept
          </v-btn>
        </v-card-actions>
            </v-card>
        </v-dialog>
        </div>
        
      </v-row>

      <div class="my-4 text-subtitle-1">
        {{movies.BoxOffice}}
      </div>

      <div> {{movies.Plot}}</div>
    </v-card-text>


    <v-card-text>
      <v-chip-group
      active-class="deep-purple accent-4 white--text"
      column>
      <v-chip v-for="genre in movies.Genre.split(',')" :key="genre">{{genre}}</v-chip>
      </v-chip-group>
    </v-card-text>

    <v-divider class="mx-4"></v-divider>

    

    
  </v-card>
</div>
</template>

<script>


export default {
  name: 'Detail',
  mounted(){
      let imdbID = this.$route.params.imdbId
     // alert(imdbID)
      fetch(`https://www.omdbapi.com/?i=${imdbID}&apikey=87d10179`)
        .then(response => response.json())
        .then(data => {
          console.log(data)
          this.movies = data
              }  
            );
  },
   data(){
    return {
      movies :{ } ,
      dialog : false       
      
    }
  }
  
  
}
</script>
